.social-links
    display: flex
    justify-content: space-between
    align-items: center
    &__text
        font-weight: 700
        font-size: 16px
        color: $main-color
    &__divider
        width: 70px
        height: 2px
        background-color: $main-color
    &__link
        width: 20px
        height: 20px
        text-decoration: none
        color: $main-color
        &:hover
            color: $main-color  
        &_nav-menu
            color: black
            margin: 0 7.5px 0 7.5px
            &:hover
                color: black 
    &_nav-menu
        position: absolute
        bottom: 50px
        left: 50%
        transform: translateX(-50%)
    &_header
        width: 340px
        height: 24px
        position: absolute
        left: 50px
        top: 50%
        transform: translate(-50%) rotate(-90deg)


.icon-github-brands
    &:before 
        font-family: 'icomoon', sans-serif
        font-size: 20px
        line-height: 20px
        content: "\e900"

.icon-square-twitter-brands
    &:before 
        font-family: 'icomoon', sans-serif
        font-size: 20px
        line-height: 20px
        content: "\e902"

.icon-instagram-brands
    &:before
        font-family: 'icomoon', sans-serif
        font-size: 20px
        line-height: 20px
        content: "\e901"
