.promo
    position: relative
    min-height: 100vh
    background: url('../img/header_bg.jpg') center (center/cover) no-repeat
    padding-top: 205px
    &__btns
        margin-top: 25px
        width: 250px
        display: flex 
        justify-content: space-between
        align-items: center
    &__link
        text-align: center
        font-weight: 700
        font-size: 14px
        line-height: 21px
        color: $main-color
        text-decoration: none
        cursor: pointer
        &:hover
            color: $main-color         
    &__burger
        position: absolute
        right: 30px
        top: 20px
        height: 21px
        width: 36px
        cursor: pointer
        display: flex
        flex-direction: column
        justify-content: space-between
        align-items: flex-end
    &__burger-item
        display: block
        height: 3px
        width: 30px
        background-color: $main-color
        &:nth-child(2)
            width: 36px
            