.nav-menu
    position: absolute
    top: 0
    left: -100%
    z-index: 999
    width: 100%
    height: 100%
    background: rgba(255, 255, 255, 0.75)
    transition: all 0.5s linear
    &_active
       left: 0
    &__wrapper
        position: relative
        width: 345px
        height: 100%
        padding-top: 140px
        background-color: $second-color
    &__list
        width: 211px
        margin-left: 75px
        padding-inline-start: 0
        list-style-type: none
    &__item
        height: 36px
        margin-top: 15px
        position: relative
        &:first-child
            margin-top: 0
        &::before
            content: ''
            position: absolute
            left: -35px
            top: 50%
            transform: translateY(-50%)
            width: 15px
            height: 15px
            border-radius: 100%
            border: 1px solid black
    &__link
        text-decoration: none
        text-transform: uppercase
        font-weight: 700
        font-size: 24px
        color: $main-color
        &:hover
            color: black
    &__close
        width: 29px
        height: 29px
        position: absolute
        top: 34px
        right: 25px
        cursor: pointer
    &__close-item
        display: block
        width: 41px
        height: 5px
        border-radius: 2px
        background-color: black
        &:first-child
            transform: rotate(45deg) translateY(4px)
        &:last-child
            transform: rotate(-45deg) translateY(-3px)
            

            