.about
    padding-top: 120px
    &__grid
        width: 1040px
        height: 600px
        display: grid
        grid-template: repeat(4, minmax(100px, auto))/ 1.5fr 1fr 1fr
        column-gap: 70px
    &__photo
        grid-row: 1/5
    &__titles
        margin-top: 6px
        grid-column: 2/4
    &__cross
        margin-top: 4px
        height: 3px
        width: 60px
        background-color: $second-color
    &__text
        grid-column: 2/4
        margin-top: 20px
        font-weight: 500
        font-size: 14px
        line-height: 21px
        color: #000000
    &__topic-wrapper
        margin-top: 40px
        display: grid
        grid-column: 2/4
        grid-template: minmax(100px, auto) / repeat(2, 1fr)
        grid-auto-rows: minmax(100px, auto)
        gap: 20px 50px
    &__topic
        font-weight: 500
        font-size: 12px
        line-height: 18px
        color: #000000

